function HeaderComponent () {
  const headerItems = ['Y', 'Top', 'New'];
  return (
    <div>
      <ul>
        {headerItems.map(item => {
          <li key{...item}>{item}</li>
        })}

        <li>{headerItems[0]}</li>
        <li>{headerItems[1]}</li>
      </ul>
    </div>
  )
}

export default HeaderComponent;